export default {
  0x0100: 'ImageWidth',
  0x0101: 'ImageLength',
  0x0102: 'BitsPerSample',
  0x0103: 'Compression',
  0x0106: 'PhotometricInterpretation',
  0x010e: 'ImageDescription',
  0x010f: 'Make',
  0x0110: 'Model',
  0x0111: 'StripOffsets',
  0x0112: {
    name: 'Orientation',
    description: value => {
      if (value === 1) {
        return 'top-left';
      }

      if (value === 2) {
        return 'top-right';
      }

      if (value === 3) {
        return 'bottom-right';
      }

      if (value === 4) {
        return 'bottom-left';
      }

      if (value === 5) {
        return 'left-top';
      }

      if (value === 6) {
        return 'right-top';
      }

      if (value === 7) {
        return 'right-bottom';
      }

      if (value === 8) {
        return 'left-bottom';
      }

      return 'Undefined';
    }
  },
  0x0115: 'SamplesPerPixel',
  0x0116: 'RowsPerStrip',
  0x0117: 'StripByteCounts',
  0x011a: 'XResolution',
  0x011b: 'YResolution',
  0x011c: 'PlanarConfiguration',
  0x0128: {
    name: 'ResolutionUnit',
    description: value => {
      if (value === 2) {
        return 'inches';
      }

      if (value === 3) {
        return 'centimeters';
      }

      return 'Unknown';
    }
  },
  0x012d: 'TransferFunction',
  0x0131: 'Software',
  0x0132: 'DateTime',
  0x013b: 'Artist',
  0x013e: 'WhitePoint',
  0x013f: 'PrimaryChromaticities',
  0x0201: 'JPEGInterchangeFormat',
  0x0202: 'JPEGInterchangeFormatLength',
  0x0211: 'YCbCrCoefficients',
  0x0212: 'YCbCrSubSampling',
  0x0213: {
    name: 'YCbCrPositioning',
    description: value => {
      if (value === 1) {
        return 'centered';
      }

      if (value === 2) {
        return 'co-sited';
      }

      return 'undefined ' + value;
    }
  },
  0x0214: 'ReferenceBlackWhite',
  0x8298: {
    name: 'Copyright',
    description: value => value.join('; ')
  },
  0x8769: 'Exif IFD Pointer',
  0x8825: 'GPS Info IFD Pointer'
};